<template>
  <div class="ranking">    
    <div class="rangking-head">
      <van-nav-bar title="" left-text="返回" left-arrow @click-left="back">
        <van-icon name="search" slot="right" size="20" color="black" />
      </van-nav-bar>
    </div>
    <div class="rangking-text">
      <div class="text-fl">
        <van-sidebar v-model="index">
          <van-sidebar-item
            :title="item.title"
            v-for="(item,index) in tlist"
            :key="index"
            :name="item.name"
            @click="clik(item.name)"
          />
        </van-sidebar>
      </div>
      <div class="text-fr">
        
        <ul>
          <li v-for="(item,index) in this.total" :key="index" :id='item.Id' @click="fontbook({name: 'particulars', params: {id: item.Id}})">
            <div class="diner-fl">
              <img :src="'https://imgapi.jiaston.com/BookFiles/BookImages/'+item.Img" class="max-img" />
            </div>
            <div class="diner-fr">
              <span class="icon-sort" :class="'icon'+(index+1)"></span>
              <p>{{item.Name}}</p>
              <p>
               {{item.Desc}}
              </p>
              <p>
                <span>{{item.CName1}}</span>
                <span>{{item.CName2}}</span>
              </p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      index: 0,
      tlist: [
        { title: "推荐榜", name: "commend" },
        { title: "收藏榜", name: "collect" },
        { title: "新书榜", name: "new" },
        { title: "完结榜", name: "over" }
      ],
      total:'',
    };
  },
  computed: {},
  methods: {
      fontbook(o){
     this.$router.push(o);
      },
    clik(name) {
         //开启加载提示
      // this.$toast.loading({
      //   duration: 0,
      //   message: '加载中...'
      // });
      this.$axios({
        methods: "GET",
        dataType: "jsonp",
        url: "http://localhost:1000/getjson/xiaoshuo-" + name
      }).then(reche => {
        reche.data.data.BookList.forEach(v=>{
            v.CName1=v.CName.slice(0,2);
            v.CName2=v.CName.slice(2,4);
        })
        this.total=reche.data.data.BookList;
      
        //  this.$toast.clear();
      });
    },
     back(){
       this.$router.go(-1);
      },
  },
  components: {},
  created() {
     
      this.$axios({
        methods: "GET",
        dataType: "jsonp",
        url: "http://localhost:1000/getjson/xiaoshuo-" + "commend"
      }).then(reche => {
          
        this.total=reche.data.data.BookList;
        reche.data.data.BookList.forEach(v=>{
            v.CName1=v.CName.slice(0,2);
            v.CName2=v.CName.slice(2,4);
        })
        
      });
  }
};
</script>
<style lang="less">
.max-img {
  display: block;
  width: 100%;
  height: 100%;
}
.ranking {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: white;
  p {
    margin: 0;
  }
  .rangking-head {
    width: 100%;
    overflow: hidden;
    .van-nav-bar__left {
      .van-icon,
      span {
        color: black;
      }
    }
  }
  .rangking-text {
    width: 100%;
    height: calc(100% - 46px);
    .text-fl {
      width: 75px;
      height: 100%;
      float: left;
      .van-sidebar {
        width: 75px;
        height: 100%;
        background: #fafafa;
        .van-sidebar-item--select {
          border-color: #33b18c;
          color: #33b18c;
        }
        a {
          font-size: 12px;
          text-align: center;
        }
      }
    }
    .text-fr {
      float: left;
      width: calc(100% - 75px);
      height: 100%;
      overflow-y: auto;
      ul {
        overflow: hidden;
        li {
          float: left;
          width: calc(100% - 34px);
          padding: 17px;
          border-bottom: 1px solid #f1f1f1;
          .diner-fl {
            float: left;
            width: 80px;
            height: 94px;
            margin-right: 15px;
          }
          .diner-fr {
            float: left;
            width: calc(100% - 95px);
            .icon-sort {
              float: right;
              width: 13px;
              height: 15px;
              display: inline-block;
              background-repeat: no-repeat;
              background-size: 100% 100%;
            }
            .icon1{
                background-image: url('../../image/t1.png');
            }
            .icon2{
                background-image: url('../../image/t2.png');
            }
            .icon3{
                background-image: url('../../image/t3.png');
            }
            p {
              max-width: 100%;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            p:nth-child(2) {
              width: 90%;
              margin-top: 0;
              font-weight: 400;
              font-size: 15px;
            }
            p:nth-child(3) {
              display: -webkit-box;
              -webkit-box-orient: vertical;
              overflow: hidden;
              margin-top: 9px;
              font-size: 13px !important;
              color: #8888;
              -webkit-line-clamp: 2;
              white-space: normal;
            }
            p:nth-child(4) {
              margin-top: 9px;
              font-size: 13px !important;
              span::before {
                content: "";
                position: absolute;
                top: -50%;
                left: -50%;
                right: -50%;
                bottom: -50%;
                border: 1px solid #b8b8b8;
                border-radius: 20px;
                transform: scale(0.5);
              }
              span {
                position: relative;
                display: inline-block;
                margin-left: 1px;
                margin-right: 15px;
                padding: 0 5px;
                font-size: 12px;
              }
            }
          }
        }
      }
    }
  }
}
</style>